html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
  max-width: 130rem;
}
@media (min-width: 992px) {
  .contenedor {
    width: 80%;
  }
}

a {
  text-decoration: none;
}

a:hover {
  color: initial;
}

h1 {
  font-size: 4.2rem;
  margin: 0 0 5rem 0;
}

h2,
h3 {
  margin: 0 0 3rem 0;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  border: none;
}

.boton-fireBrick {
  background-color: #AC2C28;
  color: #fff;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3.75px;
  border: 1px solid #83211e;
  display: inline-block;
}
.boton-fireBrick:hover {
  background-color: #83211e;
  color: #fff;
}

.seccion {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.header {
  background-color: #000;
  min-height: 30rem;
}
@media (min-width: 768px) {
  .header {
    min-height: 15rem;
    display: flex;
  }
}
.header a img {
  width: 100%;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header a img {
    width: 75%;
  }
}
@media (min-width: 1500px) {
  .header a img {
    width: 50%;
  }
}
.header .contenido-header {
  padding: 2rem;
  max-width: none;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.no-webp .imagen-header {
  background-image: url(/build/img/bufalos_header2.jpg);
}

.webp .imagen-header {
  background-image: url(/build/img/bufalos_header2.webp);
}

.imagen-header {
  background-position: center center;
  background-size: cover;
  position: relative;
  min-height: 20rem;
}
@media (min-width: 768px) {
  .imagen-header {
    min-height: 50rem;
  }
}

.contenido-imagen {
  font-family: "Suez One", serif;
  position: absolute;
  display: none;
  bottom: 0rem;
  left: 10rem;
  padding: 2rem;
}
@media (min-width: 1500px) {
  .contenido-imagen {
    bottom: 1rem;
  }
}
.contenido-imagen h2 {
  margin-bottom: 1rem;
  color: #EDEDED;
  letter-spacing: 0.5rem;
}
.contenido-imagen p {
  display: inline-block;
  color: #AC2C28;
  text-transform: uppercase;
  font-size: 6rem;
  letter-spacing: 2.5rem;
}
@media (min-width: 768px) {
  .contenido-imagen {
    display: block;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #EDEDED;
  font-size: 2rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #AC2C28;
}

.imagen {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.imagen .overlay-header {
  position: absolute;
  height: 100%;
  width: 100%;
}
.imagen img {
  width: 100%;
  display: block;
}

.contenido-destacado {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .contenido-destacado {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenido {
    margin-top: 0;
  }
}
.contenido h2 {
  margin-bottom: 2rem;
  color: #AC2C28;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1rem;
}
.contenido p {
  line-height: 1.8;
}

.no-webp .imagen-central {
  background-image: url(/build/img/imagen_central.jpg);
}

.webp .imagen-central {
  background-image: url(/build/img/imagen_central.webp);
}

.imagen-central {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.imagen-central .overlay-central {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(30, 43, 22, 0.5);
  top: 0;
  left: 0;
}

.contenido-central {
  z-index: 10;
  text-align: center;
}
.contenido-central h3, .contenido-central p {
  color: #fff;
}

.contenedor-noticias {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-noticias {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.noticia {
  background-color: #fbf3e7;
  border-radius: 3.75px;
  border: 1px solid #EDEDED;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .noticia {
    margin-bottom: 0;
  }
}
.noticia:hover {
  box-shadow: 0 0 20px 2px #ac2c28;
}
.noticia img {
  border-top-left-radius: 3.75px;
  border-top-right-radius: 3.75px;
}
.noticia .contenido-noticias {
  padding: 2rem;
}
.noticia .contenido-noticias h3 {
  margin-bottom: 2rem;
  color: #AC2C28;
}
.noticia .contenido-noticias p {
  font-size: 1.6rem;
  line-height: 1.8;
}
.noticia .contenido-noticias .fecha {
  font-style: italic;
  color: #AC2C28;
  text-align: right;
}

.site-footer {
  background-color: #000;
  padding: 1rem;
  min-height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #fff;
  font-size: 1.2rem;
}

@media (min-width: 768px) {
  .contenido-historia {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.texto-historia {
  margin-top: 2rem;
}
.texto-historia h2 {
  color: #AC2C28;
}

.musico p {
  font-size: 1.6rem;
}